.dash {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: top;
}

.loader {
    border-top: 16px solid gold;
    border-right: 16px solid rgb(95, 95, 95);
    border-bottom: 16px solid gold;
    border-left: 16px solid rgb(95, 95, 95);
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
  }
  
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.mark {
    width: 20pt;
}

.black {
}

.white {
    filter: invert(1);
}

table, th, td {
    border: 2px solid black;
}

table {
    position: relative;
    background-color: gold;
    width: 95%;
}

details {
    color: blue;
}

#modal {
    z-index: 1;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: top;
    width: 700px;
    position: absolute;
    top: 20%;
    background: #bf1e2e;
    border: 1px solid gold;
    transition: 1.1s ease-out;
    box-shadow: 
      -2rem 2rem 2rem rgba(black, 0.2);
    filter: blur(0);
    transform: scale(1);  
    opacity: 1;

    font-size: 16pt;
  }

#fade {
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.7);
    width: 100%;
    height: 100%;
}

.close {
    position: relative;
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
  }

@media only screen and (max-width: 600px) {

    #modal {
        position: absolute;
        display: block;
        left: 2.5vw;
        width: 95vmin;
      }
    table {
        width: 90vmin;
        left:1vw;
        right: 1vw;
    }
    .mark {
        width: 4.3vmin;
    }
  }

th, td {
    background-color: rgb(160, 160, 160);
    text-align: left;
}

td:hover {
    background-color: rgb(243, 255, 81);
}